<template lang="pug">
	.modal(:class='{ "is-active": isActive }')
		.modal-background
		.modal-content: .box
			.control.is-grouped.columns
				.control.column
					input.input.is-large(type='text', placeholder='')
				.control.column
					input.input.is-large(type='text', placeholder=' ')
			a.control.button.is-primary.is-large.is-fullwidth 
		button.modal-close(@click='toggle')
		//TODO Spinning button
</template>

<script>
	export default {
		props: ['isActive', 'toggle']
	}
</script>


